import Axios from "axios"
import { BaseUrl } from "../shared/BaseUrl"
import { myHeaders } from "../shared/MyHeaders"

const getRooms = async (page) => {
    const { data } = await Axios.get(`${BaseUrl}/rooms?page=${page}&size=10`, {
        headers: myHeaders
    })
    return data
}

const getAllRooms = async () => {
    const { data } = await Axios.get(`${BaseUrl}/rooms?page=0&size=1000000000`, {
        headers: myHeaders
    })
    return data
}

export {
    getRooms,
    getAllRooms
}